.video-feed-container {
  width: 100%;
  height: 100vh;
  aspect-ratio: 16 / 9;
  padding-top: 56.25%; /* 16:9 Aspect Ratio */
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 640px;
  max-height: 340px;
}

.video{
    width: 100%;
    height: 100vh;
    aspect-ratio: 16 / 9;
    border-radius: 8px;
    border: 2px solid var(--color-text-special-glow);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 640px;
    max-height: 340px;
}

.video-placeholder{
    width: 100%;
    height: 100vh;
    aspect-ratio: 16 / 9;
    max-width: 640px;
    max-height: 340px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    color: var(--color-text-special);
    text-shadow: 0 0 10px var(--color-text-special-glow);
    background-color: var(--color-surface-alt);
    border: 1px solid var(--color-text-special-glow);
}

.recording-indicator{
  width: 100%;
  height: 100vh;
  aspect-ratio: 16 / 9;
  max-width: 640px;
  max-height: 340px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  color: red;
  text-shadow: 0 0 10px #ff4949;
  background-color: #380000;
  border: 1px solid red;
}